.testimonials {
  margin-bottom: 168px;
  &__wrap-title {
    width: 1146px;
    margin: 148px auto 0;
  }
  &__name {
    font-weight: bold;
    font-size: 13px;

    letter-spacing: 3px;
    text-transform: uppercase;

    color: #c0cdd7;
  }
  &__title {
    font-weight: 600;
    font-size: 32px;

    color: #253757;
    margin-bottom: 85px;
  }
  &__wrapper {
    display: grid;
    grid-template-columns: repeat(3, 314px);
    grid-template-rows: 494px;
    justify-content: center;
    grid-column-gap: 102px;
  }
  &__card {
    background: #fff;
    box-shadow: 0 9px 18px rgba(37, 41, 49, 0.03);
    border-radius: 24px;
    position: relative;
  }
  &__image {
    @include imageBgr(#56ccf2);
  }
  &__image-red {
    @include imageBgr(#f25656);
  }
  &__image-yellow {
    @include imageBgr(#fcc304);
  }
  &__photo {
    border-radius: 50%;
  }
  &__text {
    margin: 10px 0 0;
    width: 250px;
    font-family: Open Sans, sans-serif;
    font-weight: 600;
    font-size: 13px;
    text-align: center;
    color: #6c788b;
    position: absolute;
    left: 50%;
    transform: translate(-50%);
  }
  &__quotes {
    margin: 82px auto 0;
    position: relative;
    left: 50%;
    transform: translate(-50%);
  }
  &__card-name {
    position: absolute;
    bottom: 56px;
    left: 50%;
    transform: translate(-50%);
    font-family: Open Sans, sans-serif;
    font-weight: 600;
    font-size: 13px;

    color: #253757;
  }
  &__position {
    font-weight: bold;
    font-size: 13px;
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    white-space: nowrap;
    bottom: 32px;
    text-align: center;
    letter-spacing: 3px;
    text-transform: uppercase;

    color: #c0ccd7;
  }
}
